<template>
  <div>
    <v-card>
      <v-card-title v-if="heading">
        {{ heading }}
        <v-spacer></v-spacer>
      </v-card-title>
      <v-card-text>
        <v-form
          id="itemForm"
          ref="form"
          v-model="valid"
          enctype="multipart/form-data"
        >
          <slot name="crud-form" :item="item"></slot>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          v-if="goBackButton"
          :color="goBackButton.color ? goBackButton.color : 'error'"
          @click="goBack"
        >
          {{
            goBackButton.text
              ? goBackButton.text
              : $t('crudAdd.goBackButton.text')
          }}
        </v-btn>
        <v-btn
          v-if="button"
          :loading="loading.add"
          :color="button.color ? button.color : 'success'"
          @click="saveForm"
        >
          {{ button.text ? button.text : $t('crudAdd.addButton.text') }}
        </v-btn>
      </v-card-actions>
    </v-card>

    <!-- Alert Snacbar -->
    <CrudSnackbar :alert="alert" />
  </div>
</template>

<script>
import crud from '@/mixins/crud'
import CrudSnackbar from '@/components/crud/CrudSnackbar'
export default {
  components: { CrudSnackbar },
  mixins: [crud],
  props: {
    heading: { type: String, default: '' },
    defaultItem: {
      type: Object,
      default: () => {
        return {}
      }
    },
    button: { type: [Boolean, Object], default: false },
    goBackButton: { type: [Boolean, Object], default: false },
    fetchData: { type: [Boolean, Object], default: false },
    addData: { type: [Boolean, Object], default: false }
  },
  data() {
    return {
      valid: false,
      item: Object.assign({}, this.defaultItem)
    }
  },
  // fetch item if fetch data
  async created() {
    if (this.fetchData) {
      await this.fetchItem()
    }
  },
  methods: {
    // save form
    saveForm() {
      this.$refs.form.validate()
      if (this.valid) {
        this.itemSave(this.$refs.form)
      }
    },

    goBack() {
      if (this.goBackButton.redirect) {
        this.$router.push({ path: this.goBackButton.url })
      } else {
        this.$router.go(-1)
      }
    },

    // item save
    async itemSave(form) {
      if (!this.addData) {
        this.$emit('saveItem', this.item, form)
      } else {
        await this.addItem(this.item, form)
      }
    }
  }
}
</script>

<style scope></style>
